import React from 'react';
import Swal from 'sweetalert2';
// import withReactContent from 'sweetalert2-react-content';
import './App.css';

const initialSate = {
	image: {
		original: [],
		compressed: [],
	},
	width: "",
	height: "",
	extention: "",
	quality: "",
	isFileLoaded: false,
};

const qualities = [
	{
		lable: 1,
		value: 1
	},
	{
		lable: 0.9,
		value: 0.9
	},
	{
		lable: 0.8,
		value: 0.8
	},
	{
		lable: 0.7,
		value: 0.7
	},
	{
		lable: 0.6,
		value: 0.6
	},
	{
		lable: 0.5,
		value: 0.5
	},
	{
		lable: 0.4,
		value: 0.4
	},
	{
		lable: 0.3,
		value: 0.3
	},
	{
		lable: 0.2,
		value: 0.2
	},
	{
		lable: 0.1,
		value: 0.1
	},
	{
		lable: 0,
		value: 0
	},
];

const extentions = [
	{
		lable: "JPG, JPEG",
		value: "image/jpeg",
	},		{
		lable: "PNG",
		value: "image/png",
	},
	{
		lable: "SVG",
		value: "image/svg+xml",
	},
	{
		lable: "TIF, TIFF",
		value: "image/tiff",
	},
	{
		lable: "BMP",
		value: "image/bmp",
	},
	{
		lable: "GIF",
		value: "image/gif",
	},
	{
		lable: "ICO",
		value: "image/vnd.microsoft.icon",
	},
	{
		lable: "WEBP",
		value: "image/webp",
	},
];

export default class App extends React.Component {

	constructor(props) {
		super(props);

		this.state = initialSate;

		this.fileInput = React.createRef();
		this.previewText = React.createRef();
		this.previewImg = React.createRef();

		this.handleDragEnter = this.handleDragEnter.bind(this);
		this.handleDragLeave = this.handleDragLeave.bind(this);
		this.handleDragOver = this.handleDragOver.bind(this);
		this.handleDrop = this.handleDrop.bind(this);
		this.handleClick = this.handleClick.bind(this);
		this.handleFileChange = this.handleFileChange.bind(this);
		this.handleWidthChange = this.handleWidthChange.bind(this);
		this.handleHeightChange = this.handleHeightChange.bind(this);
		this.handleApplyCick = this.handleApplyCick.bind(this);
		this.handleExtentionChange = this.handleExtentionChange.bind(this);
		this.handleQualityChange = this.handleQualityChange.bind(this);
		this.handleDownloadClick = this.handleDownloadClick.bind(this);
		this.handleResetCick = this.handleResetCick.bind(this);
	}

	componentDidMount() {
		document.title = "Free Image Commression Tool";
	}

	handleDragEnter(e) {
		e.preventDefault();
		e.stopPropagation();
		e.target.style.opacity = 0.5;

		return false;
	}
	
	handleDragLeave(e) {
		e.preventDefault();
		e.stopPropagation();
		e.target.style.opacity = '';

		return false;
	}
	
	handleDragOver(e) {
		e.preventDefault();
		e.stopPropagation();

		// Don't add file more than 1
		if(e.dataTransfer.items.length > 1) {
			e.dataTransfer.dropEffect = "none";		
		}

		return false;
	}
	
	handleDrop(e) {
		e.preventDefault();
		e.stopPropagation();
		e.target.style.opacity = '';

		this.fileInput.current.files = e.dataTransfer.files;

		this.handleFileChange();

		return false;
	}

	handleClick(e) {
		this.fileInput.current.click();
	}

	handleFileChange() {
		// Get file object from input
		let file = this.fileInput.current.files[0];

		// Reset states
		this.setState(initialSate);

		// Check file exist
		if(file) {

			// Accpet only Image
			if(file.type.match('image.*')) {

				this.compress(file);

				return true;

			} else {

				// Get extention of File
				// let extention = file.name.slice((file.name.lastIndexOf(".") - 1 >>> 0) + 2);
				let extention = file.name.slice((Math.max(0, file.name.lastIndexOf(".")) || Infinity) + 1);
				extention = extention ? extention : "Unknown";

				// Promt error
				Swal.fire({
					position: 'top-end',
					icon: 'error',
					title: `${extention} not supported`,
					showConfirmButton: false,
					timer: 1500
				});

				// Reset input file
				this.fileInput.current.value = null;

			}

		}

		// Reset to default
		this.previewText.current.hidden = false;
		this.previewImg.current.src = "";
		this.previewImg.current.hidden = true;

		return false;
	}

	compress(file) {
		const blobURL = URL.createObjectURL(file);
		const reactThis = this;
		const img = new Image();
		img.src = blobURL;
		img.onerror = function () {
			URL.revokeObjectURL(this.src);

			// Reset states
			reactThis.setState(initialSate);

			// Handle the failure properly
			Swal.fire({
				position: 'top-end',
				icon: 'error',
				title: 'Cannot load image',
				showConfirmButton: false,
				timer: 1500
			});
		};
		img.onload = function () {

			// Set image original state value
			reactThis.setState((state, props) => ({
				image: {
					...state.image,
					original: {
						file: file,
						width: img.naturalWidth,
						height: img.naturalHeight,
					}
				},
				width: (state.width === "") ? img.naturalWidth : state.width,
				height: (state.height === "") ? img.naturalHeight : state.height,
				extention: (state.extention === "") ? file.type : state.extention,
				quality: (state.quality === "") ? 0.3 : state.quality,
				isFileLoaded: true,
			}));

			// URL.revokeObjectURL(this.src);
			const canvas = document.createElement("canvas");
			canvas.width = reactThis.state.width;
			canvas.height = reactThis.state.height;
			const ctx = canvas.getContext("2d");
			ctx.drawImage(img, 0, 0, reactThis.state.width, reactThis.state.height);
			canvas.toBlob(
				(blob) => {

					// Check if commressed image size not more than original
					if(blob.size < file.size) {

						file = reactThis.blobToFile(blob);

					}

					// Set preview IMG
					reactThis.previewText.current.hidden = true;
					reactThis.previewImg.current.src = URL.createObjectURL(file);
					reactThis.previewImg.current.hidden = false;

					// Set image original state value
					reactThis.setState((state, props) => ({
						image: {
							...state.image,
							compressed: {
								file: file,
								width: reactThis.state.width,
								height: reactThis.state.height,
							}
						}
					}));

				},
				reactThis.state.extention,
				reactThis.state.quality
			);
		};
	}

	formatBytes(bytes, decimals = 2) {
		if (bytes === 0) return '0 Bytes';
	
		const k = 1000;
		const dm = decimals < 0 ? 0 : decimals;
		const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
	
		const i = Math.floor(Math.log(bytes) / Math.log(k));
	
		return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
	}

	blobToFile(blob) {
		return new File([blob], this.state.image.original.file.name, {
			type: blob.type,
		});
	}

	handleWidthChange(e) {

		if (e.target.value >= 1 && e.target.value <= 9999) {

			let ratio = this.ratio();

			this.setState({
				width: parseInt(e.target.value),
				height: Math.round(e.target.value / ratio),
			});

		}

	}

	handleHeightChange(e) {

		if (e.target.value >= 1 && e.target.value <= 9999) {

			let ratio = this.ratio();

			this.setState({
				height: parseInt(e.target.value),
				width: Math.round(e.target.value * ratio),
			});

		}

	}

	ratio () {
		return this.state.image.original.width / this.state.image.original.height;
	}

	handleApplyCick() {

		if(this.state.width && this.state.height) {
			this.compress(this.fileInput.current.files[0]);
		}

	}

	handleExtentionChange(e) {
		this.setState({
			extention: e.target.value,
		});
	}

	handleQualityChange(e) {
		this.setState({
			quality: parseFloat(e.target.value),
		});
	}

	handleDownloadClick() {
		const link = document.createElement('a');
		link.href = URL.createObjectURL(this.state.image.compressed.file);
		link.download = "";
		link.click();
	}

	handleResetCick() {
		this.setState({
			width: this.state.image.original.width,
			height: this.state.image.original.height,
			extention: this.state.image.original.file.type,
			quality: 0.3,
		});
	}

	render() {

		let selectAreaText;

		if(window.screen.width < 768) {
			selectAreaText = "Tap to select Image";
		} else {
			selectAreaText = "Drag or Select image";
		}

		let originalFile = this.state.image.original.file;
		let compressedFile = this.state.image.compressed.file;

		let reduceFileSizePercentage = "--";

		if(originalFile && compressedFile) {
			reduceFileSizePercentage = (((compressedFile.size/originalFile.size) * 100) - 100).toFixed(2) + "%";
		}

		return (
			<React.Fragment>
				<div className="flex flex-col text-center w-full">
					<h1 className="sm:text-3xl text-2xl font-medium title-font mb-4 text-gray-900">Free Image Commression Tool</h1>
					<p className="lg:w-2/3 mx-auto leading-relaxed text-base">With the below compression tool you can compress any image at minimum size. Please prefer JPEG in customization for better result.</p>
					<h2 class="text-xs text-red-500 tracking-widest font-medium title-font mb-1">The best part about the online compressor is that your image is compressed on your device, which means it is not stored on any server. Please feel free to use this tool.</h2>
				</div>
				<div className="container px-5 pt-24 mx-auto">
					<div className="grid md:grid-cols-3 gap-4 -mx-4 -mb-10 text-center">
						<div className="md:col-span-2 mb-10 px-4">
							<div
								draggable="true"
								onDragEnter={this.handleDragEnter}
								onDragOver={this.handleDragOver}
								onDragLeave={this.handleDragLeave}
								onDrop={this.handleDrop}
								onClick={this.handleClick}
								className="drag-select cursor-pointer no-select-text h-96 flex flex-wrap content-center justify-center border-dashed border-4 rounded-2xl overflow-hidden"
							>

								<input ref={this.fileInput} type="file" name="image" accept="image/*" onChange={this.handleFileChange} hidden />
								<span ref={this.previewText} className="font-black text-2xl text-red-600">{selectAreaText}</span>
								<img ref={this.previewImg} alt="content" className="object-contain object-center h-full w-full" hidden />

							</div>

							<div className="md:flex mt-5">
								<span className="title-font font-medium text-2xl text-gray-900">({ originalFile ? this.formatBytes(originalFile.size) : "--"}/{compressedFile ? this.formatBytes(compressedFile.size) : "--"}) = {reduceFileSizePercentage}</span>
								<button type="button" disabled={!this.state.isFileLoaded} onClick={this.handleDownloadClick} className="flex ml-auto text-white bg-green-500 border-0 py-2 px-5 focus:outline-none hover:bg-green-600 rounded">Download</button>
							</div>
						</div>

						<div className="md:col-span-1 mb-10 px-4">
							<h2 className="title-font text-2xl font-medium text-gray-900 mt-6 mb-3">Customization</h2>

							<div className="flex flex-wrap -m-2">
								<div className="p-2 w-1/2">
									<div className="relative">
										<label htmlFor="width" className="leading-7 float-left text-sm text-gray-600">Width</label>
										<input type="number" id="width" name="width" value={this.state.width} onChange={this.handleWidthChange} disabled={!this.state.isFileLoaded} className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-red-500 focus:bg-white focus:ring-2 focus:ring-red-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
									</div>
								</div>
								<div className="p-2 w-1/2">
									<div className="relative">
										<label htmlFor="height" className="leading-7 float-left text-sm text-gray-600">Height</label>
										<input type="number" id="height" name="height" value={this.state.height} onChange={this.handleHeightChange} disabled={!this.state.isFileLoaded} className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-red-500 focus:bg-white focus:ring-2 focus:ring-red-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
									</div>
								</div>
								<div className="p-2 w-1/2">
									<div className="relative">
										<label htmlFor="extention" className="leading-7 float-left text-sm text-gray-600">Extentions</label>
										<select name="extention" id="extention" value={this.state.extention} onChange={this.handleExtentionChange} disabled={!this.state.isFileLoaded} className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-red-500 focus:bg-white focus:ring-2 focus:ring-red-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out">
											<option value="" disabled={this.state.isFileLoaded}>Select</option>
											{extentions.map((extention, index) =>
												<option key={index} value={extention.value}>{extention.lable}</option>
											)};
										</select>
									</div>
								</div>
								<div className="p-2 w-1/2">
									<div className="relative">
										<label htmlFor="quality" className="leading-7 float-left text-sm text-gray-600">Quality</label>
										<select name="quality" id="quality" value={this.state.quality} onChange={this.handleQualityChange} disabled={!this.state.isFileLoaded} className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-red-500 focus:bg-white focus:ring-2 focus:ring-red-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out">
											<option value="" disabled={this.state.isFileLoaded}>Select</option>
											{qualities.map((quality, index) =>
												<option key={index} value={quality.value}>{quality.lable}</option>
											)};
										</select>
									</div>
								</div>
							</div>

							<button disabled={!this.state.isFileLoaded} onClick={this.handleResetCick} className="inline float-left mt-6 text-white bg-gray-500 border-0 py-2 px-5 focus:outline-none hover:bg-gray-600 rounded">Reset</button>
							<button disabled={!this.state.isFileLoaded} onClick={this.handleApplyCick} className="inline float-right mt-6 text-white bg-green-500 border-0 py-2 px-5 focus:outline-none hover:bg-green-600 rounded">Apply</button>
						</div>
					</div>
				</div>

				<div className="container px-5 pt-24 mx-auto">
					<div className="md:flex md:flex-wrap -mx-4 -mb-12 text-center">

						<div className="md:w-1/2 mb-10 px-4">
							<span className="title-font text-xl font-medium text-gray-900 mt-6 mb-3">Original</span>

							<div className="flex border-t border-gray-200 py-2">
								<span className="text-gray-500">Name</span>
								<span className="ml-auto text-gray-900">{originalFile ? originalFile.name : "--"}</span>
							</div>

							<div className="flex border-t border-gray-200 py-2">
								<span className="text-gray-500">Width * Height</span>
								<span className="ml-auto text-gray-900">{this.state.image.original.width ?? "--"} * {this.state.image.original.height ?? "--"}</span>
							</div>

							<div className="flex border-t border-gray-200 py-2">
								<span className="text-gray-500">Size</span>
								<span className="ml-auto text-gray-900">{originalFile ? this.formatBytes(originalFile.size) : "--"}</span>
							</div>

							<div className="flex border-t border-gray-200 py-2">
								<span className="text-gray-500">MIME Type</span>
								<span className="ml-auto text-gray-900">{originalFile ? originalFile.type : "--"}</span>
							</div>

							<div className="flex border-t border-b mb-6 border-gray-200 py-2">
								<span className="text-gray-500">Last Modified</span>
								<span className="ml-auto text-gray-900">{originalFile ? originalFile.lastModifiedDate.toLocaleString() : "--"}</span>
							</div>
						</div>

						<div className="md:w-1/2 mb-10 px-4">
							<span className="title-font text-xl font-medium text-gray-900 mt-6 mb-3">Compressed</span>

							<div className="flex border-t border-gray-200 py-2">
								<span className="text-gray-500">Name</span>
								<span className="ml-auto text-gray-900">{compressedFile ? compressedFile.name : "--"}</span>
							</div>

							<div className="flex border-t border-gray-200 py-2">
								<span className="text-gray-500">Width * Height</span>
								<span className="ml-auto text-gray-900">{this.state.image.compressed.width ?? "--"} * {this.state.image.compressed.height ?? "--"}</span>
							</div>

							<div className="flex border-t border-gray-200 py-2">
								<span className="text-gray-500">Size</span>
								<span className="ml-auto text-gray-900">{compressedFile ? this.formatBytes(compressedFile.size) : "--"}</span>
							</div>

							<div className="flex border-t border-gray-200 py-2">
								<span className="text-gray-500">MIME Type</span>
								<span className="ml-auto text-gray-900">{compressedFile ? compressedFile.type : "--"}</span>
							</div>

							<div className="flex border-t border-b mb-6 border-gray-200 py-2">
								<span className="text-gray-500">Last Modified</span>
								<span className="ml-auto text-gray-900">{compressedFile ? compressedFile.lastModifiedDate.toLocaleString() : "--"}</span>
							</div>
						</div>

					</div>
				</div>
			</React.Fragment>
		);
	}
}
